import React, { useEffect, useState } from 'react'
import { Button, Box } from '@material-ui/core';

function PagiButtons ( { number, btnSwitchPage }) {
    const [activeBtn, setActiveBtn] = useState('outlined')
    const [currentNum, setCurrentBtn] = useState(number)
    function changeBtn() {
        setCurrentBtn(number)
    }
    console.log('status', activeBtn, number);
    useEffect(() => {
        setActiveBtn('outlined')
    }, [number])

    return <Box mr={1}>
                <Button 
                    variant={activeBtn}
                    color="secondary"
                    onClick={(e) => {
                        btnSwitchPage(number)
                        console.log(number)
                    }} 
                >
                {number} 
                </Button>
            </Box>
}

export default PagiButtons