import React, { useState } from 'react' 
import { Button, Box } from '@material-ui/core';
import PagiButtons from './PagiButton'

function Pagination ({ btnSwitchPage, countTodoOnPage, countFilterTodo }) {

    const pageNum = []

    for (let i = 1; i <= Math.ceil(countFilterTodo / countTodoOnPage); i++) {
        pageNum.push(i)
    }

    
    return (
        <Box display='flex' justifyContent='center'>
            {pageNum.map((btn, i) => {
                return (
                <PagiButtons number={btn} key={i} btnSwitchPage={btnSwitchPage}/>
                // <Box mr={1} key = {i}>
                //     <Button 
                //         id={i}
                //         variant="outlined" 
                //         color="secondary"
                //         onClick={(e) => {
                //             btnSwitchPage(btn)
                //         }} 
                //     >
                //     {btn} 
                //     </Button>
                // </Box>
                )
            })}
        </Box>
    )
}

export default Pagination